"use client";

import { useAthlete } from "@/components/athlete/AthleteStore";
import { mockClasses } from "@/lib/mockClasses";
import ClassCard from "@/components/athlete/ClassCard";

export default function Page() {
  const { bookedIds, toggleBooking } = useAthlete();

  const booked = mockClasses.filter((c) => bookedIds.includes(c.id));

  return (
    <div>
      <h1 className="text-xl font-semibold">Mis reservas</h1>

      <div className="mt-4 grid gap-3">
        {booked.length === 0 && (
          <p className="text-sm text-gray-500">
            Todavía no tenés reservas.
          </p>
        )}

        {booked.map((c) => (
          <ClassCard
            key={c.id}
            item={c}
            isBooked
            bookedCount={0}
            onToggle={toggleBooking}
          />
        ))}
      </div>
    </div>
  );
}
