"use client";

import { useMemo } from "react";
import { formatWhen } from "@/lib/format";
import type { ClassItem } from "@/lib/mockClasses";

type Props = {
  item: ClassItem;
  isBooked: boolean;
  bookedCount: number;
  onToggle: (id: string) => void;
};

export default function ClassCard({
  item,
  isBooked,
  bookedCount,
  onToggle,
}: Props) {
  const spotsLeft = useMemo(
    () => Math.max(0, item.capacity - bookedCount),
    [item.capacity, bookedCount]
  );

  const disabled = !isBooked && spotsLeft === 0;

  return (
    <div className="rounded-2xl border border-gray-200 bg-white p-4 shadow-sm">
      <div className="flex items-start justify-between gap-3">
        <div>
          <div className="text-sm text-gray-500">{formatWhen(item.startsAt)}</div>
          <div className="mt-1 text-base font-semibold">{item.title}</div>
          <div className="mt-1 text-sm text-gray-500">Coach: {item.coach}</div>
        </div>

        <div className="text-right">
          <div className="text-xs text-gray-500">Cupos</div>
          <div className="text-sm font-semibold">
            {spotsLeft}/{item.capacity}
          </div>
        </div>
      </div>

      <button
        onClick={() => onToggle(item.id)}
        disabled={disabled}
        className={`mt-3 w-full rounded-xl px-4 py-2 text-sm font-medium ${
          disabled
            ? "bg-gray-100 text-gray-400"
            : isBooked
            ? "bg-gray-900 text-white"
            : "bg-white text-gray-900 border border-gray-300"
        }`}
      >
        {isBooked ? "Cancelar" : spotsLeft === 0 ? "Sin cupo" : "Reservar"}
      </button>
    </div>
  );
}
